import axios from '@/utils/axios';

export async function fetchSupplierList() {
  const res = (await axios({
    method: 'post',
    url: '/anta-material/supplier/list/condition',
    data: {
      status: '1',
    },
  })).data;
  return res;
}

export async function fetchSupplierAll() {
  const res = (await axios({
    method: 'post',
    url: '/anta-material/supplier/list/condition',
    data: {},
  })).data;
  return res;
}

export async function fetchSupplierNotBound() {
  const res = (await axios({
    method: 'get',
    url: '/anta-material/supplier/searchOriginalSupplierNameByNotBound',
  })).data;
  return res;
}

export default fetchSupplierNotBound;
