import axios from './axios';

/* ============================== 鞋模型的对象相关 ============================= */
/**
 * 读取设置项
 * @returns {Promise}
 */
export const getWorkspaceSetting = () => {
  return axios({
    method: 'get',
    url: '/msgcenter/config/info',
  });
};

/* ============================== 鞋模型的对象相关 ============================= */
/**
 * 更改设置项
 * @param {Object} data 设置：包括type和value
 * @returns {Promise}
 */
export const setWorkspaceSetting = (data) => {
  return axios({
    method: 'post',
    url: '/msgcenter/config/update',
    data,
  });
};
