<template>
  <div class="header-container">
    <div class="header">
      <!-- Logo -->
      <router-link :to="{ path: '/' }" class="header-logo">
        <img :src="logo" alt="logo" :width="width" />
      </router-link>

      <div class="header-info">
        <!-- 上传下拉框 -->
        <!-- <el-dropdown placement="bottom">
          <i class="iconfont icon-add-circle element-icon text-link" :hidden="!$can('system', 'OP0113')" />

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              v-for="item in uploadMenu"
              :key="item.id"
              @click.native="handleJumpToUpload(uploadMenu, item.route)">
              <div class="text-one-line">
                <i :class="item.icon" class="iconfont" />
                <span>{{ item.text }}</span>
              </div>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->

        <el-dropdown
          @command="handleSetLang"
          placement="bottom"
          v-if="cooperation !== 'anta'"
        >
          <div class="header-language">
            <i class="iconfont iconhuanjing_nol"></i>
            <font>{{ currentLangName }}</font>
            <i class="iconfont icon-caret-down"></i>
          </div>

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              class="header-language-item"
              v-for="item in langConfigList"
              :key="item.num"
              :command="item.num"
            >
              <div>
                <img :src="item.icon" />
                <font>{{ item.name }}</font>
              </div>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>

        <el-tooltip
          v-if="clientDownloadLink"
          content="下载客户端"
          effect="dark"
          style="position: relative"
        >
          <a
            class="i-upload_nol element-icon text-link"
            :href="clientDownloadLink"
          />
        </el-tooltip>

        <!-- 申请成为品牌供应商入口 -->
        <el-tooltip
          key="2bsupplier"
          content="申请成为品牌供应商"
          effect="dark"
          v-if="canSupply2BSupplier"
        >
          <i
            @click="supply2BSupplier"
            class="iconfont iconkehubianhao element-icon text-link"
          ></i>
        </el-tooltip>

        <!-- 虚拟样品入口 -->
        <el-tooltip
          :content="$t('public.header.iconTip.order_manage')"
          effect="dark"
          v-if="user && user.tradeFairWorkspace"
        >
          <i
            class="iconfont iconkehubianhao element-icon text-link"
            @click="$router.push({ name: 'TaskOrder' })"
          />
        </el-tooltip>

        <!-- 虚拟样品入口 -->
        <el-tooltip
          :content="$t('public.header.iconTip.virtual_sample')"
          effect="dark"
        >
          <i
            class="iconfont element-icon text-link"
            :class="isCloth ? 'iconxuniyangpinfuwu-yifu' : 'icon-house'"
            @click="$router.push({ name: 'ServiceSample' })"
          />
        </el-tooltip>

        <!-- 消息中心 -->
        <el-tooltip
          v-if="user"
          :content="$t('public.header.iconTip.notification')"
          effect="dark"
          style="position: relative"
        >
          <i
            @click="showInfoCenter"
            class="iconfont icon-bell-o element-icon"
            :class="{ badge: readRecord && readRecord.unreadTotal > 0 }"
          >
          </i>
        </el-tooltip>

        <!-- 新手指引 -->
        <el-tooltip
          :content="$t('public.header.iconTip.help_center')"
          effect="dark"
          style="position: relative"
        >
          <i
            class="iconfont icon-circle-help element-icon"
            @click="$router.push({ name: 'HelpCenter' })"
          />
        </el-tooltip>

        <!-- 意见反馈入口 -->
        <el-tooltip
          :content="$t('public.header.iconTip.opinion')"
          effect="dark"
          style="position: relative"
        >
          <i
            class="iconfont iconwentifankui element-icon"
            @click="showOpinionCenter"
          />
        </el-tooltip>

        <!-- 用户信息 -->
        <div v-if="user" style="width: 240px">
          <el-dropdown @command="handleDropdownCommand" placement="bottom">
            <div class="header-info-user" v-if="user">
              <BaseImage
                class="user-avatar"
                :src="this.userImage"
                fit="cover"
              />
              <div class="user-detail">
                <p class="user-name text-one-line">
                  {{ user.name || user.account }}
                </p>
                <p class="user-role text-one-line">
                  {{ userBusinessType }}
                </p>
                <p class="expiration-tip text-one-line">
                  {{
                    $t("public.header.tips.exp_until", {
                      days: user.daysRemaining,
                    })
                  }}
                </p>
              </div>
            </div>

            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="profile" v-if="can113">
                <div class="text-one-line">
                  <i class="iconfont icon-person" />
                  {{ $t("public.header.dropdownMenu.profile_settings") }}
                </div>
              </el-dropdown-item>
              <el-dropdown-item
                command="setting"
                v-if="can113"
                :divided="can113"
              >
                <div class="text-one-line">
                  <i class="iconfont icon-gear" />
                  {{ $t("public.header.dropdownMenu.settings") }}
                </div>
              </el-dropdown-item>
              <el-dropdown-item command="logout">
                <div class="text-one-line">
                  <i class="iconfont icon-logout" />
                  {{ $t("public.header.dropdownMenu.log_out") }}
                </div>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import InfoCenter from '@/views/notification';
import BaseImage from '@/components/base/Image'
import SideSlider from '@/components/SideSlider';
import StartGuide from '@/views/guide/components/StartGuide';
import WorkspaceSettings from '@/views/file-list/components/workspace/WorkspaceSettings'
import Opinion from '@/views/opinion/index'

import { ApigetShade, ApiaddShade } from '@/api/account';
import { getGuideStatus } from '@/api/guide';
import { getMyUploadIds } from '@/api/catalogue';
import { logout, getContextUser } from '../../loaders/context';
import setLang, {
  languageNumber2lang,
  langMapLangNum,
  langConfigList,
} from '@/router/utils/set-lang';
import { getCurrentCompany } from '../../loaders/company';
import useBusinessTypes from '@/utils/composables/use-business-types'
import envs from '../../utils/envs';
import Supply2BSupplier from '../form/Supply2BSupplier';
import axios from '@/utils/axios';
import { defineComponent } from '@vue/composition-api';
import { updateSystemPermissions } from '@/loaders/permission';
/** @typedef {import('@/models/User').User} User */
const antaLogo = require('@/assets/img/header/logo-anta.png');

const ideationLogo = require('@/assets/img/header/logo-header.png');
export default {
  name: 'Header',
  components: {
    BaseImage,
    StartGuide,
  },
  setup() {
    return {
      businessTypes: useBusinessTypes()
    }
  },
  filters: {
    type(value) {
      const types = ['Brands', 'Material Supplier', 'Designer', 'Subaccount'];
      return types[value];
    },
  },
  data() {
    return {
      rect: '',
      body: '',
      which: 1,
      /** @type {any} */
      readRecord: {},
      infoCenterSign: false,
      timer: undefined, //消息中心未读数获取计时器
      uploadMenu: [
        {
          text: i18n.t('public.header.dropdownMenu.upload_design'),
          icon: 'icon-cube',
          route: {
            name: 'UploadModel',
            query: { parentFolderId: this.user?.shoeUploadCId || 0 },
          },
        },
        {
          text: i18n.t('public.header.dropdownMenu.upload_material'),
          icon: 'icon-material',
          route: {
            name: 'MaterialUpload',
            query: { parentFolderId: this.user?.materialUploadCId || 0 }
          }
        },
      ],
      helpMenu: [
        { id: 0, text: i18n.t('public.header.dropdownMenu.user_tutorial_menu'), icon: 'icon-file-list-video', param: 1, },
        { id: 1, text: i18n.t('public.header.dropdownMenu.maneuvering_3d_models'), icon: 'icon-renderer-video', param: 2, },
        { id: 2, text: i18n.t('public.header.dropdownMenu.uploading_files'), icon: 'icon-upload-video', param: 3, },
      ],
      tmpLang: 0,
      langConfigList,
    }
  },
  computed: {
    /** @return {boolean} */
    canSupply2BSupplier() {
      if (!this.isMaterialSupplier) return false;
      // if (!this.cooperation.includes('anta')) return false;
      // 如果已经有权限，说明已经通过了，就不再显示入口
      if (this.$can('', '6001', false)) return false;
      return true;
    },
    /** @return {string} */
    clientDownloadLink() {
      const apiDomain = envs.VUE_APP_API_DOMAIN;
      if (this.user && this.$can('', '6001', false)) {
        return `//${apiDomain}/datapipe/short/static/anta/latest/IdeationInstaller-4001.exe`;
      } else if (this.user && this.user.businessType === 3) {
        return `//${apiDomain}/datapipe/short/static/anta/latest/IdeationInstaller-4002.exe`
      }
    },
    /** @return {boolean} */
    isIndependentDesigner() {
      return this.user?.mail === 'designer_helper@163.com';
    },
    /** @return {boolean} */
    can113() {
      return this.$can('', '113') && !this.isIndependentDesigner;
    },
    /** @return { User } */
    user() {
      return getContextUser();
    },
    /** @return { boolean } */
    isCloth() {
      return this.user && this.user.businessType === 2;
    },
    /** @return { string } */
    company() {
      return getCurrentCompany();
    },
    /** @return { string }*/
    cooperation() {
      return envs.VUE_APP_COOPERATION;
    },
    /** @return { string } */
    width() {
      return this.cooperation.includes('anta') ? '164px' : '124px';
      // return (this.user && this.user.businessType === 1) || this.cooperation.includes('anta')
      //   ? '164px' : '124px';
    },

    /** @return { string } */
    logo() {
      // return (this.user && this.user.businessType === 1) || this.cooperation.includes('anta')
      //   ? antaLogo : ideationLogo;
      return this.cooperation.includes('anta')
        ? antaLogo : ideationLogo;
    },

    /** @return { string } */
    userImage() {
      return this.user?.headImg || require('@/assets/img/header/default_avatar.png');
    },

    /** @return { string } */
    userBusinessType() {
      return this.businessTypes ?
        this.businessTypes.find(e => e.value === (this.company && this.company.type))?.[i18n.language === 'en' ? 'usName' : 'name']
        || 'loading'
        : 'loading'
    },
    /** @return {boolean} */
    isMaterialSupplier() {
      return this.company && this.company.type === 3;
    },
    /** @return {string} */
    currentLangName() {
      const currentLang = langConfigList.find(e => e.lang === i18n.language);
      return currentLang ? currentLang.name : '';
    }
  },
  async created() {
    if (this.user) {
      await this.notRead()
      if (this.readRecord?.unreadTotal === 0) {
        this.timer = setInterval(() => { this.notRead() }, 60000)
      }
    }
  },
  destroyed() {
    clearInterval(this.timer)
    this._msgCenterDialogController.close();
  },
  async mounted() {
    const GuideStatus = localStorage.getItem('guideStatus')
    // 如果未读
    if (!GuideStatus) {
      await this.$Dialog.dialogify({
        is: StartGuide,
        props: {
          isCloth: this.isCloth,
        }
      });
      // 如果是虚拟帐号，关闭弹窗后显示IM入口提醒
      if (this.user?.roleId === '3') {
        this.$emit('close-guide');
      }
    }
    this._msgCenterDialogController = this.$Dialog.dialogify({
      is: InfoCenter,
      on: {
        'update-has-new-message': readRecord => { this.readRecord = readRecord; },
      }
    }, {
      is: SideSlider
    })

    this.tmpLang = langMapLangNum(i18n.language).toString();
    this.oldLang = this.tmpLang; // 用于取消确认时回退
  },
  methods: {
    async supply2BSupplier() {
      await updateSystemPermissions([1, 4, 6, 8]);
      if (this.canSupply2BSupplier) {
        await this.$injectDialog({
          is: Supply2BSupplier
        })
      } else {
        this.$message.info('您已通过申请')
      }
    },
    // 消息中心
    async showInfoCenter() {
      await this._msgCenterDialogController;
    },
    // 打开意见反馈dialog
    async showOpinionCenter() {
      await this.$Dialog.dialogify({
        is: Opinion
      })
    },
    // 获取引导是否已读状态
    async getGuideStatus() {
      const res = await getGuideStatus();
      // this.guideNotRead = !res.data.filter(it => it.type === 1)[0].status;
      return res.data.filter(it => it.type === 1)[0].status;
    },
    // 未读消息统计
    async notRead() {
      const { data } = await axios({
        method: 'get',
        url: '/msgcenter/message/detail',
      });
      this.readRecord = data;
    },
    async handleDropdownCommand(command) {
      switch (command) {
        case 'logout':
          // Initialize the token value to empty
          await logout()
          // Jump to login page
          // this.$router.push({ name: 'Login', query: { redirect: this.$route.fullPath } });
          this.$router.push({ name: 'Login' });
          break;
        case 'profile':
          if (!this.$can('system', 'OP1001')) return
          this.$router.push({ name: 'Profile' });
          break;
        case 'setting':
          await this.$Dialog.dialogify({
            is: WorkspaceSettings,
          }, {
            props: {
              width: '496px',
            }
          })
          break;
        case 'help':
          this.showTipDeleloping();
          break;
        case 'order':
          if (this.user?.roleId === '2' || this.user?.roleId === '3') {
            this.$message.warning(i18n.t('public.error.access_denied'))
          } else {
            this.$router.push({ name: 'OrderSuit' });
          }

          break;
        default:
          this.showTipDeleloping();
      }
    },
    async handleSetLang(langNum) {
      const langConfig = langConfigList.find((item) => langNum === item.num);
      try {
        await this.$confirm(i18n.t('setting.confirm_change_language', {
          lang: i18n.t(`setting.${langConfig.lang}`),
        }), i18n.t('setting.confirm'));


        setLang(langConfig.lang);
      } catch (e) {
        this.tmpLang = this.oldLang;
      }
    },
    // setLang() {
    //   if (this.$i18n.locale === 'cn') {
    //     this.$i18n.locale = 'en';
    //   } else {
    //     this.$i18n.locale = 'cn';
    //   }
    // },
    async updateUploadMenu(uploadMenu) {
      const target = (await getMyUploadIds()).data;
      uploadMenu[0].route.query.parentFolderId = target.shoeUploadCId
      uploadMenu[1].route.query.parentFolderId = target.materialUploadCId
    },
    async handleJumpToUpload(uploadMenu, route) {
      if (!route.query.parentFolderId) {
        await this.updateUploadMenu(uploadMenu)
      }
      this.$router.push(route)
    }
  },
};
</script>

