import axios from '@/utils/axios';
import fetchSupplierAll from '@/loaders/supplier';
import isFilled from '@/utils/validators/is-filled';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import {
  computed,
  defineComponent, reactive, ref, watchEffect,
} from '@vue/composition-api';
import { ElForm } from 'element-ui/types/form';

type Supplier = {
  id: string,
  originalSupplierFullName: string,
  originalSupplierName: string,
  companyId?: string,
  name?: string
};

enum SupplierSupplimentMode {
  SELECT = 'SELECT', INPUT = 'INPUT',
}

export default defineComponent({
  props: {},
  dialogify: {
    props: {
      title: '申请成为品牌供应商',
      width: '450px',
    },
  },
  errorInjector: false,
  render() {
    if (typeof this.isApplying !== 'boolean') {
      return <div vLoading={true} style={{ height: '200px' }}></div>;
    }
    if (!this.isApplying) {
      return <div class="flex-column flex-center">
      <img src={require('@/assets/img/empty.png')} style={{ width: '186px', height: '186px' }}/>
      <span>品牌审核中，请耐心等待，谢谢！</span>
      <el-button type='primary' round onclick={() => this.$emit('confirm')} style={{
        marginTop: '24px',
      }}>好的</el-button>
    </div>;
    }
    return <section class="flex-column">
        {
          this.mode === SupplierSupplimentMode.SELECT
            ? <div class="flex-column">
                <div class="flex-vertical-center">
                  <span style={{ marginRight: '10px' }}>供应商名称</span>
                  {
                    this.list
                      ? <el-select size='small' vModel={this.id} filterable style='flex:1'>
                      {
                        this.list.map(item => <el-option label={item.originalSupplierFullName}
                        value={item.id}></el-option>)
                      }
                  </el-select>
                      : <div style={{
                        width: '310px',
                        height: '32px',
                      }} vLoading={true}></div>
                  }
                </div>
                <div
                 style={{
                   marginTop: '10px', cursor: 'pointer', marginLeft: '82px', fontSize: '12px',
                 }}>找不到公司名称？可联系品牌对接人😊</div>
            </div>
            : <el-form size='small' props={{ model: this.form }} class='flex-column' ref='form'
            labelWidth='100px' label-position='right' style={{
              flex: 1,
            }}>
              <el-form-item label="供应商名称" prop="originalSupplierName" required
              rules={validator2ElemRule(isFilled)}>
                <el-input vModel={this.form.originalSupplierName}></el-input>
              </el-form-item>
              <el-form-item label="国家" prop="country" required
              rules={validator2ElemRule(isFilled)} >
                <el-input vModel={this.form.country}></el-input>
              </el-form-item>
            </el-form>
        }
      <div class="flex" style={{ marginTop: '20px' }}>
        {
          this.mode === SupplierSupplimentMode.INPUT
            ? <el-button round onclick={() => { this.mode = SupplierSupplimentMode.SELECT; }}>返回</el-button>
            : null
        }
        <span style="flex: 1"></span>
        <el-button round onclick={() => this.$emit('cancel')}>取消</el-button>
        <el-button round onclick={this.submit} type='primary'
        disabled={this.confirmDisabled}>确定</el-button>
      </div>
    </section>;
  },
  // @ts-ignore
  setup(_, { refs, root: { $nextTick, $message }, emit }) {
    const isApplying = useLoader(async () => {
      return (await axios({
        url: '/anta-material/supplierRecord/selectSupplierRecordByCompanyId',
        method: 'GET',
      })).data as boolean;
    }, {}, {
      initialValue: null,
      loadingValue: () => null,
    });
    const mode = ref(SupplierSupplimentMode.SELECT);
    const list = useLoader(async () => {
      const res = await fetchSupplierAll();
      return res as Supplier[];
    }, {}, {
      initialValue: null,
    });
    const id = ref<string>();
    const form = reactive({
      originalSupplierName: '',
      country: '',
    });
    const confirmDisabled = computed(() => {
      if (mode.value === SupplierSupplimentMode.SELECT) {
        if (id.value) return false;
        return true;
      }
      if (!!form.originalSupplierName && !!form.country) return false;
      return true;
    });
    watchEffect(async () => {
      if (mode.value === SupplierSupplimentMode.INPUT) {
        await $nextTick();
        await $nextTick();
        (refs.form as ElForm).clearValidate();
      }
    });
    async function submit() {
      const url = '/anta-material/supplierRecord/save';
      let data:Record<string, string> = {};
      if (mode.value === SupplierSupplimentMode.SELECT) {
        data.supplierId = id.value!;
      } else {
        data = form;
      }
      await axios({
        url,
        data,
        method: 'POST',
      });
      $message.success('申请提交成功，可随时查看此入口或消息中心了解审核情况');
      emit('confirm');
    }
    return {
      isApplying,
      mode,
      list,
      id,
      form,
      confirmDisabled,

      submit,
    };
  },
});
