          @change="value => changeSettingWrapper({ type:1, value:Number(value) })"></el-switch>
      </div>
      <div class="flex-space-between">
        <span>
          {{ $t('workspace.file_change_notification') }}
        </span>
        <el-switch v-model="notifyWhenResourceChange"
          @change="value => changeSettingWrapper({ type:2, value:Number(value) })"></el-switch>
      </div>
      <div class="flex-space-between">
        <span>
