<script>
import i18n from 'i18next';
import {
  getWorkspaceSetting,
  setWorkspaceSetting,
} from '@/api/setting';

export default {
  data() {
    return {
      notifyWhenUserChange: true,
      notifyWhenResourceChange: true,
      notifyWhenAtMe: true,
      notifyBrowserPush: true,

      // tmpLang: 0,
    };
  },
  async mounted() {
    const result = (await getWorkspaceSetting()).data;
    this.notifyWhenUserChange = !!result.user;
    this.notifyWhenResourceChange = !!result.resource;
    this.notifyWhenAtMe = !!result.at;
    this.notifyBrowserPush = !!result.browser;

    // this.tmpLang = langMapLangNum(i18n.language).toString();
    // this.oldLang = this.tmpLang; // 用于取消确认时回退
  },
  methods: {

    /**
     * type:  0：浏览器推送，1：变更成员邮件，2：变更资源邮件，3：@邮件
     * value: 开启为1，关闭为0
     * 摘自http://yapi.4dshoetech.local/project/52/interface/api/4967
     */
    async changeSetteingItem({ type, value }) {
      await setWorkspaceSetting({ type, value });
      if (type === 0) {
        localStorage.setItem('notifyBrowserPush', !!value);
      }
    },
    changeSettingWrapper(...args) {
      this.$act(this.changeSetteingItem, [...args], i18n.t('setting.changed'));
    },
  },
};
</script>

